import { initCatalog } from './js/catalog.js';
import { createMoviePopup } from './js/popup.js';
import { showLoader, hideLoader } from './js/loader.js';

import './js/library.js';
async function loadPartials() {
  const loads = document.querySelectorAll('load');

  for (const el of loads) {
    const src = el.getAttribute('src');
    if (src) {
      try {
        const res = await fetch(src);
        if (res.ok) {
          const html = await res.text();
          const wrapper = document.createElement('div');
          wrapper.innerHTML = html;
          el.replaceWith(wrapper);
        } else {
          console.error('Partial y√ºklenemedi:', src);
        }
      } catch (err) {
        console.error('Y√ºkleme hatasƒ±:', err);
      }
    }
  }
}

// ‚úÖ Partial'lar y√ºklendikten sonra her b√∂l√ºm√º ba≈ülat
loadPartials().then(async () => {
  try {
    // CATALOG HERO b√∂l√ºm√º varsa ba≈ülat
    const heroSection = document.querySelector('.catalog-hero');
    if (heroSection) {
      setupHeroEvents();
    }

    // CATALOG b√∂l√ºm√º varsa mod√ºl√º dinamik olarak i√ße aktar ve ba≈ülat
    const catalogSection = document.getElementById('catalog');
    if (catalogSection) {
      const { initCatalog } = await import('./js/catalog.js');
      if (typeof initCatalog === 'function') {
        initCatalog();
      }
    }

    // UPCOMING b√∂l√ºm√º varsa ba≈ülat
    const upcomingSection = document.getElementById('upcoming');
    if (upcomingSection) {
      renderUpcoming();
    }

    // FOOTER varsa modal'ƒ± ba≈ülat
    const footerEl = document.getElementById('footer');
    if (footerEl) {
      initFooterModal();
    }
  } catch (error) {
    console.error('B√∂l√ºmler ba≈ülatƒ±lƒ±rken hata olu≈ütu:', error);
  }
});
