export const API_KEY = '2609725f661288e9b08bc0d62455b671';
export const BASE_URL = 'https://api.themoviedb.org/3';

// T√ºm filmler
export async function fetchPopularMovies(page = 1) {
  const res = await fetch(
    `${BASE_URL}/movie/popular?api_key=${API_KEY}&page=${page}`
  );
  return await res.json();
}

// G√ºnl√ºk trend filmler
export async function fetchDailyTrending() {
  const res = await fetch(`${BASE_URL}/trending/movie/day?api_key=${API_KEY}`);
  return await res.json();
}

// Haftalƒ±k trend filmler
export async function fetchWeeklyTrending() {
  const res = await fetch(`${BASE_URL}/trending/movie/week?api_key=${API_KEY}`);
  return await res.json();
}

// Yeni filmler (upcoming)
export async function fetchUpcomingMovies() {
  const res = await fetch(`${BASE_URL}/movie/upcoming?api_key=${API_KEY}`);
  return await res.json();
}

// Film detaylarƒ± (tek bir film)
export async function fetchMovieDetails(movieId) {
  const res = await fetch(`${BASE_URL}/movie/${movieId}?api_key=${API_KEY}`);
  return await res.json();
}

// Filmin videolarƒ± (fragman dahil)
export async function fetchMovieVideos(movieId) {
  const res = await fetch(
    `${BASE_URL}/movie/${movieId}/videos?api_key=${API_KEY}`
  );
  return await res.json();
}

// Anahtar kelime + yƒ±la g√∂re arama
export async function searchMovies(query, year = '') {
  const res = await fetch(
    `${BASE_URL}/search/movie?api_key=${API_KEY}&query=${encodeURIComponent(
      query
    )}&year=${year}`
  );
  return await res.json();
}

// T√ºr listesini getirme
export async function fetchGenres() {
  const res = await axios.get(`${BASE_URL}/genre/movie/list`, {
    params: { api_key: API_KEY, language: LANGUAGE },
  });
  return res.data;
}
